<template>
  <div>
    <loading :active.sync="isLoading">
      <template slot="default">
        <div class="loadingio-spinner-eclipse">
          <div class="loading-style">
            <div></div>
            <div></div>
            <div></div>
          </div>
        </div>
      </template>
    </loading>
    <div class="topCover product-bg bg-img d-flex">
      <h3 class="coverTitle productTitle">{{ product.title }}</h3>
    </div>
    <div class="container">
      <div class="breadcrumb">
        <ul class="list-unstyled d-flex align-items-center">
          <li>
            <router-link class="nav-link text-main" to="/"></router-link>
          </li>
          <li class="mr-1">></li>
          <li>
            <router-link class="nav-link text-main" to="/products"></router-link>
          </li>
          <li class="mr-1">></li>
          <li>{{ product.title }}</li>
        </ul>
      </div>
      <div class="d-flex align-items-center row mb-6">
        <img class="col-md-5 col-sm-12 prodcutImg" :src="product.imageUrl[0]" />
        <div class="col-md-5 col-sm-12 px-lg-1 px-sm-3">
          <h2 class="text-left mb-4 text-main font-weight-bold">{{ product.title }}</h2>
          <p class="text-left mb-4">{{ product.content }}</p>
          <div class="d-flex justify-content-between">
            <p class="mr-lg-6 mr-md-3 lineThrough text-secondary">{{ product.origin_price | money }} </p>
            <p class="font-weight-bold text-danger">{{ product.price | money }} </p>
          </div>
          <div class="d-flex justify-content-between">
            <select name="unit" v-model="product.num" class="px-lg-6 px-md-3 py-1 mr-3">
              <option :value="num" v-for="num in 5" :key="num">{{ num }} </option>
            </select>
            <button
              type="button"
              class="btn btn-main d-flex justify-content-start"
              @click.prevent="goToCart(product.id, product.num)"
            ></button>
          </div>
        </div>
      </div>
      <ul class="nav nav-tabs mb-3" id="myTab" role="tablist">
        <li class="nav-item" role="presentation">
          <a
            class="nav-link active tab-active"
            id="home-tab"
            data-toggle="tab"
            href="#home"
            role="tab"
            aria-controls="home"
            aria-selected="true"
          ></a>
        </li>
        <li class="nav-item" role="presentation">
          <a
            class="nav-link tab-active"
            id="contact-tab"
            data-toggle="tab"
            href="#contact"
            role="tab"
            aria-controls="contact"
            aria-selected="false"
          ></a>
        </li>
      </ul>
      <div class="tab-content mb-6" id="myTabContent">
        <div
          class="text-left tab-pane fade show active"
          id="home"
          role="tabpanel"
          aria-labelledby="home-tab"
        >
          <p class="mb-3"> 3~5 </p>
          <p> KLSIKO  2 </p>
        </div>
        <div
          class="text-left tab-pane fade"
          id="contact"
          role="tabpanel"
          aria-labelledby="contact-tab"
        >
          <p class="mb-3"> KLSIKO  KLSIKO </p>
          <p> 0800-000-000</p>
        </div>
      </div>
      <h3 class="text-main text-left font-weight-bold mb-6"></h3>
      <RelatedProducts :product="product" @update="getProduct" />
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
import Alert from '@/alert.js'
import RelatedProducts from '@/components/front/RelatedProducts.vue'

export default {
  data () {
    return {
      isLoading: false,
      product: {
        num: 1,
        imageUrl: []
      }
    }
  },
  components: {
    RelatedProducts
  },
  created () {
    this.getProduct()
  },
  methods: {
    getProduct () {
      const { id } = this.$route.params
      const url = `${process.env.VUE_APP_APIPATH}${process.env.VUE_APP_UUID}/ec/product/${id}`
      this.isLoading = true
      this.$http
        .get(url)
        .then((res) => {
          this.product = {
            ...res.data.data,
            num: 1
          }
          this.isLoading = false
        })
        .catch(() => {
          this.isLoading = false
        })
    },
    goToCart (id, quantity = 1) {
      this.isLoading = true
      const url = `${process.env.VUE_APP_APIPATH}${process.env.VUE_APP_UUID}/ec/shopping`
      const cart = {
        product: id,
        quantity: quantity
      }
      this.$http
        .post(url, cart)
        .then((res) => {
          this.$bus.$emit('in-cart')
          Alert.fire({
            title: '',
            icon: 'success'
          })
          this.isLoading = false
        })
        .catch((err) => {
          Alert.fire({
            title: `${err.response.data.errors}`,
            icon: 'warning'
          })
          this.isLoading = false
        })
    }
  },
  mounted () {
    $('#myTab a').on('click', function (e) {
      e.preventDefault()
      $(this).tab('show')
    })
  }
}
</script>

